.card {
  background-color: var(--base-100);
  border-radius: 60px;
  flex-direction: column;
  justify-content: space-between;
  height: 640px;
  max-height: 75vh;
  padding: 40px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.dm-gsap-cards {
  transform-origin: top;
  will-change: transform;
  position: sticky;
  top: 0;
}

.card:before {
  content: "";
  filter: blur(20px);
  z-index: 0;
  background: linear-gradient(360deg, #0505054d 0%, #050505cc 100%);
  width: 100%;
  height: 40%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.card__header {
  z-index: 10;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.card__advantages {
  flex-wrap: wrap;
  align-items: center;
  gap: 4px;
  display: flex;
}

.card__advantage {
  -webkit-backdrop-filter: blur(40px);
  text-transform: uppercase;
  text-wrap: nowrap;
  white-space: nowrap;
  background: #ffffff1a;
  border-radius: 20px;
  outline: 1px solid #fff3;
  justify-content: center;
  align-items: center;
  height: 50px;
  padding: 20px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
  display: flex;
  position: relative;
  box-shadow: 0 4px 30px #0000001a;
}

.card__content {
  z-index: 10;
  margin-top: 40px;
  position: relative;
}

.card__title {
  font-family: var(--font-family-third);
  letter-spacing: .02em;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(90deg, #fff 0%, #5b6468 100%) text;
  width: 100%;
  margin-bottom: 65px;
  font-size: 48px;
  font-weight: 500;
  line-height: 120%;
}

.card.card-1 {
  z-index: 10;
}

.card.card-2 {
  z-index: 11;
}

.card.card-3 {
  z-index: 12;
}

.card.card-4 {
  z-index: 13;
}

.card.card-1 .card__title {
  max-width: 402px;
}

.card.card-2 .card__title {
  max-width: 620px;
}

.card.card-3 .card__title {
  max-width: 720px;
}

.card.card-4 .card__title {
  max-width: 970px;
}

.card.orange-card:before {
  filter: blur(40px);
  background: linear-gradient(#131313 0%, #000 100%);
}

.card:after {
  content: "";
  filter: blur(12px);
  z-index: 5;
  background: linear-gradient(0deg, #05050599 0% 100%);
  height: 30%;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.card.orange-card:after {
  filter: blur(20px);
  background: linear-gradient(360deg, #ff7b2226 0%, #ff7b220f 100%);
  height: 30%;
}

.orange-card .card__title {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(61deg, #fff 0%, #ff5f20 100%) text;
}

.card__bg {
  object-fit: cover;
  object-position: center;
  position: absolute;
  inset: 0;
}

.card__bg img {
  width: 100%;
  height: 100%;
  display: block;
}

.card.card-2 .card__bg {
  opacity: .4;
}

.card__image {
  width: 100%;
  max-width: 1040px;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
}

.card.card-2 .card__image {
  max-width: 651px;
}

.card.card-3 .card__image {
  max-width: 950px;
}

.card__footer {
  z-index: 10;
  align-items: end;
  gap: 60px;
  margin-top: auto;
  padding-top: 34px;
  display: flex;
  position: relative;
}

.card__footer-button.button {
  text-transform: none;
  height: 58px;
  padding: 24px 28px;
}

.card.orange-card .card__footer-button.button {
  -webkit-backdrop-filter: blur(40px);
  background-color: #ff5f2033;
}

.card__parameters {
  grid-template-columns: 1fr 1fr;
  gap: 20px 0;
  width: 100%;
  max-width: 825px;
  display: grid;
}

.card__parameter {
  color: #fffc;
  margin-left: 12px;
  font-size: 16px;
  font-weight: 400;
  line-height: 140%;
  list-style: outside;
}

.card__parameter::marker {
  color: var(--green-100);
  font-size: 20px;
}

.orange-card .card__parameter::marker {
  color: var(--orange-100);
}

.card__footer-description {
  font-family: var(--font-family-third);
  letter-spacing: .02em;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(90deg, #fff 0%, #5b6468 100%) text;
  font-size: 28px;
  font-weight: 500;
  line-height: 120%;
}

.card.orange-card .card__footer-description {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(61deg, #fff 0%, #ff5f20 100%) text;
}

.card.card-1 .card__parameters {
  grid-template-columns: 1fr;
}

.card__header-description {
  font-family: var(--font-family-first);
  color: var(--base-content-100);
  opacity: .6;
  font-size: 16px;
  font-weight: 400;
  line-height: 140%;
}

.card__video-wrapper {
  opacity: .4;
  position: absolute;
  inset: 0;
}

.card__video {
  object-fit: cover;
  object-position: top center;
  width: 100%;
  height: 100%;
}

@media (width <= 1024px) {
  .card {
    border-radius: 60px;
    padding: 30px;
  }

  .card__content {
    margin-top: 34px;
  }
}

@media (width >= 900px) and (height <= 768px) {
  .card {
    height: 400px;
  }

  .card__title {
    margin-bottom: 20px;
    font-size: 24px;
  }

  .card__content {
    margin-top: 28px;
  }

  .card__parameters {
    row-gap: 16px;
  }

  .card__footer {
    align-items: center;
    margin-top: auto;
    padding-top: 0;
  }

  .card__footer-description {
    font-size: 24px;
  }

  .card .card__image {
    max-width: 70%;
  }

  .card.card-2 .card__image {
    max-width: 60%;
    right: 3%;
  }

  .card.card-3 .card__image {
    max-width: 70%;
    right: 5%;
  }

  .card__footer-button.button {
    height: auto;
    padding: 10px 32px;
  }
}

@media (width <= 768px) {
  .card {
    border-radius: 60px;
    justify-content: start;
    height: 755px;
    max-height: 65vh;
    padding: 30px;
  }

  .card__advantage {
    border-radius: 14px;
    height: 39px;
    padding: 16px 20px;
    font-size: 10px;
  }

  .card__content {
    margin-top: 34px;
  }

  .card__title {
    margin-bottom: 34px;
    font-size: 24px;
  }

  .card__parameters {
    grid-template-columns: 1fr;
    gap: 10px;
  }

  .card__parameter {
    font-size: 12px;
  }

  .card__footer {
    margin-top: unset;
    flex-direction: column;
    flex-grow: 1;
    justify-content: space-between;
    align-items: start;
    gap: 12px;
    padding-top: 0;
    display: flex;
  }

  .card__footer-description {
    text-align: left;
    width: 100%;
    margin-top: 20px;
    font-size: 18px;
    font-weight: 500;
  }

  .card__footer-button.button {
    width: 100%;
  }

  .card__advantages {
    flex-wrap: nowrap;
  }

  .card__image {
    height: 400px;
    top: unset;
    object-fit: cover;
    object-position: center center;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .card__title {
    margin-bottom: 26px;
  }

  .card__header-description {
    margin-left: 10px;
    font-size: 12px;
  }

  .card-2 .card__parameter:nth-last-child(-n+3), .card-3 .card__parameter:last-child {
    display: none;
  }
}
